<h1 id="安装tengine" tabindex="-1"><a class="header-anchor" href="#安装tengine" aria-hidden="true">#</a> 安装Tengine</h1>
<blockquote>
<p>Centos8安装有问题，使用Centos7</p>
</blockquote>
<ol>
<li><strong>configure</strong> 提示缺少什么就安装什么</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>./configure
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> yum -y <span class="token function">install</span> gcc-c++
<span class="token function">sudo</span> yum <span class="token function">install</span> -y pcre pcre-devel
<span class="token function">sudo</span> yum <span class="token function">install</span> -y openssl openssl-devel
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><ol start="2">
<li><strong>make</strong></li>
</ol>
<p>CFLAGS =  -pipe  -O -W -Wall -Wpointer-arith -Wno-unused -Werror -g</p>
<p>去掉-Werror</p>
<div class="language-yaml ext-yml line-numbers-mode"><pre v-pre class="language-yaml"><code>ngx_int_t
ngx_libc_crypt(ngx_pool_t <span class="token important">*pool</span><span class="token punctuation">,</span> u_char <span class="token important">*key</span><span class="token punctuation">,</span> u_char <span class="token important">*salt</span><span class="token punctuation">,</span> u_char <span class="token important">**encrypted)</span>
<span class="token punctuation">{</span>
    char               <span class="token important">*value;</span>
    size_t              len;
    struct crypt_data   cd;

    cd.initialized = 0;
<span class="token comment">#ifdef __GLIBC__</span>
    /* work around the glibc bug <span class="token important">*/</span>
    cd.current_salt<span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span> = ~salt<span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span>;
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><p><strong>centos8安装有问题</strong></p>
